.buddies-main {
    overflow-x: hidden;
    overflow-y: scroll;

    display: flex;
    flex-direction: column;
    align-items: center;

    max-height: 94.5vh;
    position: relative;
}

.buddies-spinner {
    display: flex;
    height: 90vh;
    justify-content: center;
    align-items: center;
}

.buddies-actions {
    padding: 2vw 0 2vw 0;
    user-select: none;
}

.buddies-action {
    display: flex;
    align-items: center;
    padding: 0.5vw 0 0.5vw 2.5vw;

    text-decoration: none;
    color: black;
    font-size: 4.25vw;
    padding-left: 2vw;

    user-select: none;
}

.buddies-action-icon {
    margin-right: 2vw;
}
