.reg-main {
    display: flex;
    flex-direction: column;

    width: 100vw;
    height: 100vh;
    height: 100svh;
    max-height: 100vh;
    max-height: 100svh;
    overflow-y: scroll;
    box-sizing: border-box;
}

.reg-logo {
    width: 20vw;
}

.reg-inst-head {
    margin-bottom: 2.5vw;
    box-sizing: border-box;
    font-weight: 900;
}

.reg-header {
    display: flex;
    flex-direction: row;
    padding: 4vw 0 4vw 4vw;
    align-items: center;
    background-color: var(--surface);
    color: white;
}

.reg-head-title {
    font-size: 5vw;
    font-weight: 900;
}

.reg-head-text {
    width: 80vw;
    display: flex;
    flex-direction: column;
}

.reg-head-desc {
    font-size: 3.5vw;
}

.reg-name {
    background: white;
}

.reg-form-cont {
    display: flex;
    justify-content: center;
}

.reg-dp-main {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    padding: 5vw;
}

.reg-dp-circle {
    background-color: var(--surface);
    border-radius: 100vw;
    width: 60vw;
    height: 60vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 3vh;
    margin-bottom: 3vh;
    cursor: pointer;
}

.reg-dp-plus {
    display: flex;
    margin-bottom: 5vw;
    box-sizing: border-box;
    padding: 5vw;
}

.reg-dp-desc {
    font-size: 4vw;
    font-weight: 100;
    color: #555;
}
  
.reg-dp-cont {
    width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.reg-dp-inst {
    width: 100%;
    text-align: left;
}

.reg-in {
    color: #555;
    font-size: 4vw;
    padding: 1vw;
}

.reg-over {
    text-decoration: underline;
    color: #555;
    width: 75vw;
    text-align: center;
    margin-top: 5vw;
}