.apl-main {
    display: flex;
    width: 100%;
    justify-content: center;
}

.apl-root {
    display: flex;
    align-items: center;
    background-color: white;
    box-sizing: border-box;
    padding: 2vw 2vw 2vw 4vw;
    border-bottom: .1vw #eee solid;
}

.apl-body {
    box-sizing: border-box;
    margin-left: 2vw;
}

.apl-body {
    font-weight: 900;
    font-size: 4vw;
}

.apl-body-holder {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}

.apl-del {
    background-color: rgb(255, 42, 42);
    color: white;
}