.dropdown-root {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: white;
    border: none;
    border-radius: 1vw;
    align-items: center;
}

.dropdown-field {
    padding: 1.5vw;
    outline: none;
    border: none;
    border-radius: 1vw;
    font-size: 4vw;
    width: 90%;
    height: 4.5vw;
}

.dropdown-icon {
    margin: 0vw 1vw 0 0;
}

@keyframes dropdown-zoomin-close {
    from {
        opacity: 0;
        transform: scale(0);
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

  