.apbio-root {
    box-sizing: border-box;
    padding: 2vw;
}

.apbio-head {
    display: flex;
    flex-direction: column;

    .action-buttons {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        box-sizing: border-box;
        padding: 2vw;
    }
}

.apbio-headname {
    font-weight: 900;
    font-size: 4.5vw;
}

.apbio-head-name-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding: 2vw;

    .desc {
        text-align: center;
    }
}