.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  height: 100svh;
  max-height: 100vh;
  max-height: 100svh;
  overflow-y: scroll;
  background-color: #f5f5f5;
}

.login-logo {
  width: 60vw;
  height: 60vw;
  margin-top: 15vw;
}

.login-title {
  font-family: 'Saira Extra Condensed', sans-serif;
  font-size: 7.5vw;
  color: #555;
  margin-bottom: 15vw;
}

.login-timer {
  display: flex;
  width: 80vw;
  flex-direction: row;
  justify-content: center;
  margin: 2vw 0 2vw 0;
}

.login-label {
  color: #666;
  font-size: 4vw;
  width: 80vw;
  text-align: center;
  margin-bottom: 3vw;
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  height: 45vh;
  align-items: flex-start;
}

.login-status {
  display: flex;
  color: darkslategrey;
  height: 20vw;
  align-items: center;
  width: 70vw;
  justify-content: center;
  text-align: center;
}

.login-input {
  width: 74vw;
  height: 10vw;
  margin-bottom: 3vw;
  padding: 1vw 1vw 1vw 5vw;
  border: 1px solid #ccc;
  border-radius: 2vw;
  font-size: 5vw;
}

.login-send-button, .login-reg-button {
  width: 80vw;
  height: 10vw;
  background-color: var(--bar);
  color: #fff;
  font-size: 4vw;
  border: none;
  border-radius: 1vw;
  cursor: pointer;
}
.login-register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 20vh;
}

.login-button:disabled {
  background-color: #b0b0b0;
  cursor: not-allowed;
}

.login-reg-prompt {
  margin: 2vw;
  font-size: 4vw;
}

.login-success {
  display: flex;
  height: 100vh;
  font-size: 5vw;
  flex-direction: column;
  align-items: baseline;
  justify-content: center;
}

.login-logo-cont {
  display: flex;
  height: 35vh;
  align-items: flex-end;
}

.login-status-cont {
  display: flex;
  height: 10vh;
  flex-direction: column;
  align-items: center;
}

.login-buttons {
display: flex;
flex-direction: column;
width: 100%;
align-items: center;
margin-top: 2vw;
color: #777;
font-size: 3.75vw;
}

.login-reg-link {
  text-decoration: none;
  color: white;
  font-size: 4vw;
}