.form-main {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 85%;
}

.form-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 4vw 0 4vw 0; 
}

.form-section {
    margin-bottom: 5vw;
}

.form-section.hide {
    display: none;
}

.form-section-label {
    font-weight: 900;
    color: #555;
}

.form-navigators {
    display: flex;
    width: 100%;
    box-sizing: border-box;
    justify-content: space-evenly;
    margin-bottom: 10vw;
}

.form-navigator {
    cursor: pointer;
    background: var(--bar);
    color: white;
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    padding: 2vw 4vw 2vw 4vw;
    font-size: 4vw;
    margin: 0 2vw 0 2vw;
    border-radius: 1vw;
    border: none;
}

.form-indicators {
    display: flex;
    width: 96%;
    box-sizing: border-box;
    justify-content: space-evenly;
    margin: 2vw 0 2vw 0;
}

.form-head {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 3vw;
}

.form-mand {
    font-size: 3.5vw;
    color: #555;
}

.form-cdef {
    font-size: 3.5vw;
    background-color: #aaa;
    color: #eee;
    padding: 0.75vw 1vw 0.75vw 1vw;
    border-radius: 1vw;
    width: auto;
    text-align: center;
    margin-bottom: 1vw;
    cursor: pointer;
}

.form-navigator.disabled {
    background-color: #888;
}

.form-paginator {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-page {
    font-size: 3.5vw;
    color: #888;
}

.form-indicator {
    width: 100%;
    background-color: #aaa;
    height: 2vw;
    border-radius: 100vw;
    box-sizing: border-box;
    margin: 0 .5vw 0 .5vw;
}

.form-indicator.over {
    background-color: var(--surface);
}

@keyframes form-appear-anim {
    from {
        height: 0;
        opacity: 0;
    }
    to {
        height: 100%;
        opacity: 1;
    }
}
