.constr-text {
    margin-top: 4vw;
    width: 80vw;
    font-size: 4.5vw;
}

@media (min-width:700px) and (min-aspect-ratio: 1){
    .constr-text {
        font-size: 2vw;
        width: 60vw;
        text-align: center;
    }
}

@media (min-width:700px) and (min-aspect-ratio: 1.35){
    .constr-text {
        font-size: 1.75vw;
        width: 50vw;
        text-align: center;
    }
}