.appmenu-slider {
    position: fixed;
    height: 100vh;
    width: 70vw;
    background: var(--surface);

    left: -90vw;
    top: 0vh;
    z-index: 1000;

    max-height: 100vh;
    overflow-y: scroll;

    color: white;
    transition: left .3s ease-in-out;

    padding: 1vw 2vw 0 0;
    box-sizing: border-box;
}

.appmenu-slider.open {
    left: 0vw;
}

.appmenu-label {
    font-size: 3vw;
    opacity: .9;
    padding: 2vw;

}

.appmenu-item {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 2vw;
    font-size: 4vw;
    border-top-right-radius: 100vw;
    border-bottom-right-radius: 100vw;
}

.appmenu-item.selected {
    background: var(--selected);
}

.appmenu-item-name {
    padding-left: 2vw;
}

@media (min-width:700px) and (min-aspect-ratio: 1) {
    .appmenu-slider {
        width: 35vw;
        padding: .5vw 1vw 0 0;
    }

    .appmenu-label {
        font-size: 1.25vw;
        padding: .75vw;
    }

    .appmenu-item {
        padding: 1vw;
        font-size: 1.75vw;
    }

    .appmenu-item-name {
        padding-left: .75vw;
    }
}


@media (min-width:700px) and (min-aspect-ratio: 1.35) {

    ::-webkit-scrollbar {
        width: .5vw;
    }

    ::-webkit-scrollbar-track {
        background: var(--surface);
    }

    ::-webkit-scrollbar-thumb {
        background: #ddd;
        border-radius: 1vw;
    }

    ::-webkit-scrollbar-thumb:hover {
        background: #eee;
    }

    .appmenu-slider {
        width: 25vw;
        padding: .5vw 1vw 0 0;
    }

    .appmenu-label {
        font-size: 1.2vw;
        padding: .75vw;
    }

    .appmenu-item {
        padding: 1vw;
        font-size: 1.5vw;
    }

    .appmenu-item-name {
        padding-left: .75vw;
    }

    
}
