.assignee-box {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.assignee-wa, .assignee-call {
    background-color: grey;
    width: 0vw;
    max-width: 20vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.assignee-windup {
    transition: width 0.3s ease-in-out;
}

.assignee-course {
    color: #555;
    font-size: 3.25vw;
}

.assignee-right-info {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    margin-right: 3vw;
}

.assignee-notif-blob {
    background-color: var(--green);
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5vw;
    padding: 1vw 3vw 1vw 3vw;
}

.assignee-notif-icon {
    margin-right: 1vw;
}

.assignee-details-cont {
    display: flex;
    align-items: center;
}

.assignee-pane {
    display: flex;
    align-items: center;
}

.assignee-content {
    display: flex;
    background-color: white;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 1vw 0vw 1vw 0vw;
}

.assignee-details {
    display: flex;
    flex-direction: column;
}

.assignee-name {
    font-size: 4.25vw;
    font-weight: 600;
    margin-bottom: .5vw;
}

.assignee-buddy-name {
    margin-left: .5vw;
}

.assignee-buddy {
    font-size: 3.5vw;
    color: #555;

    display: flex;
    align-items: flex-end;
}

.assignee-dp {
    font-size: 4.25vw;
    font-weight: 600;
    margin-left: 2.5vw;
}

.assignee-dp-holder {
    display: flex;
    align-items: flex-end;
    width: 18vw;
}

.assignee-buddy-badge {
    margin-left: -6vw;
    background-color: var(--bar);
    border-radius: 100%;
    padding: 1vw;
}

@media (min-width:700px) and (min-aspect-ratio: 1){
    .assignee-content {
        padding: 0.5vw 0 0.5vw 0;
    }
}

@media (min-width:700px) and (min-aspect-ratio: 1.35){
    .assignee-content {
        padding: 0.75vw 0 0.75vw 0;
    }
}