.buddy-box {
    display: flex;
    justify-content: space-between;
}

.buddy-wa, .buddy-call {
    background-color: grey;
    width: 0vw;
    max-width: 20vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.buddy-windup {
    transition: width 0.3s ease-in-out;
}

.buddy-notif-blob {
    background-color: var(--green);
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5vw;
    padding: 1vw 3vw 1vw 3vw;
}

.buddy-notif-icon {
    margin-right: 1vw;
}

.buddy-details-cont {
    display: flex;
    align-items: center;
}

.buddy-pane {
    display: flex;
    align-items: center;
}

.buddy-content {
    display: flex;
    background-color: white;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 1vw 0vw 1vw 0vw;
}

.buddy-details {
    display: flex;
    flex-direction: column;
    padding-left: 2vw;
}

.buddy-name {
    font-size: 4.25vw;
    font-weight: 600;
    margin-bottom: .5vw;
}

.buddy-course {
    color: #555;
    font-size: 3.25vw;
}

.buddy-right-info {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    margin-right: 3vw;
}

.buddy-dp {
    font-size: 4.25vw;
    font-weight: 600;
    margin-left: 2.5vw;
}

.buddy-preacher-name {
    margin-left: .5vw;
}

.buddy-preacher {
    font-size: 3.5vw;
    color: #555;

    display: flex;
    align-items: flex-end;
}

@media (min-width:700px) and (min-aspect-ratio: 1){
    .buddy-content {
        padding: 0.5vw 0 0.5vw 0;
    }
}

@media (min-width:700px) and (min-aspect-ratio: 1.35){
    .buddy-content {
        padding: 0.75vw 0 0.75vw 0;
    }
}
