:root {
  --bar: #00415D;
  --surface: #0075a7;
  --selected: #6fa1d2;
  --bg: #fff;
  --secondaryBg: #1f2c34;
  --toastBg: #6d6c6c71;
  --green: #25d366;
  --warning: rgba(255, 85, 85, 0.75);

  --breakwidth: 700px;
  --tablet: 1;
  --desktop: 1.35; 
}

body {
  background-color: var(--bg);
  max-height: 100vw;
  overflow: hidden;
}